export default {
  props: { type: { type: String, default: 'nft' } },
  computed: {
    iconUrl() {
      try {
        return require(`~/assets/icons/${this.type}.svg`)
      } catch (e) {
        return require(`~/assets/icons/fallback.svg`)
      }
    },
  },
}
