import Chartist from 'chartist'
import { Grid, html, h, createRef as gCreateRef } from 'gridjs'
import 'gridjs/dist/theme/mermaid.css'

export default {
  data() {
    return {
      usageGrid: '',
      chartOpts: {
        height: '5.2rem',
        showPoint: true,
        fullWidth: true,
        chartPadding: { top: 0, right: 0, bottom: 0, left: 0 },
        axisX: { showGrid: false, showLabel: false, offset: 0 },
        axisY: { showGrid: false, showLabel: false, offset: 0 },
      },
    }
  },
  mounted() {
    this.usageGrid = new Grid({
      columns: [
        'No.',
        'Token',
        'Category',
        'Protocol',
        {
          name: 'Balance',
          formatter: (cell) => `$ ${cell}`,
        },
        {
          name: 'Users',
          formatter: (cell) => {
            return h(
              'div',
              {
                style: {
                  color: cell > 0 ? '#27e4bb' : '#f3566b',
                },
              },
              html(`<span>$ ${cell}</span>${cell} %`)
            )
          },
        },
        {
          name: 'Volume',
          formatter: (cell) => `$ ${cell}`,
        },
        'Fees',
        {
          name: 'Activity',
          sort: false,
          formatter: (cell) => {
            const ref = gCreateRef()
            const chart = h('div', { ref })

            setTimeout(() => {
              ref.current &&
                new Chartist.Line(
                  ref.current,
                  {
                    series: [cell],
                  },
                  this.chartOpts
                )
            }, 0)

            return chart
          },
        },
      ],
      sort: true,
      fixedHeader: true,
      style: {
        table: {
          width: '100%',
          whiteSpace: 'nowrap',
          fontFamily: 'Cera Pro, sans-serif',
          fontWeight: '500',
          textTransform: 'capitalize',
          textAlign: 'center',
        },
        th: {
          background: '#2c7df7',
          color: '#fff',
          padding: '0.6rem 1.5rem',
          border: 'none',
        },
        td: {
          border: 'none',
          padding: '1rem 1.5rem',
          background: 'transparent',
        },
      },
      data: [
        [
          1,
          'Tezos',
          'General',
          'XTZ',
          '11.80',
          '4.61',
          '402.93',
          '845,98M',
          [360, 363, 366, 361, 366, 350, 370],
        ],
        [
          2,
          'Tezos',
          'General',
          'XTZ',
          '11.80',
          '4.61',
          '402.93',
          '845,98M',
          [360, 363, 366, 361, 366, 350, 370],
        ],
        [
          3,
          'Tezos',
          'General',
          'XTZ',
          '11.80',
          '4.61',
          '402.93',
          '845,98M',
          [360, 363, 366, 361, 366, 350, 370],
        ],
        [
          4,
          'Tezos',
          'General',
          'XTZ',
          '11.80',
          '4.61',
          '402.93',
          '845,98M',
          [360, 363, 366, 361, 366, 350, 370],
        ],
        [
          5,
          'Tezos',
          'General',
          'XTZ',
          '11.80',
          '4.61',
          '402.93',
          '845,98M',
          [360, 363, 366, 361, 366, 350, 370],
        ],
      ],
    }).render(document.querySelector('#usageGrid'))
    this.updateUsage()
  },
  methods: {
    updateUsage() {
      setTimeout(() => {
        this.usageGrid
          .updateConfig({
            data: [
              [
                1,
                'Tezos',
                'General',
                'XTZ',
                '11.80',
                '4.61',
                '402.93',
                '845,98M',
                [360, 363, 366, 361, 366, 350, 370],
              ],
              [
                2,
                'Tezos',
                'General',
                'XTZ',
                '11.80',
                '4.61',
                '402.93',
                '845,98M',
                [360, 363, 366, 361, 366, 350, 370],
              ],
              [
                3,
                'Tezos',
                'General',
                'XTZ',
                '11.80',
                '4.61',
                '402.93',
                '845,98M',
                [360, 363, 366, 361, 366, 350, 370],
              ],
              [
                4,
                'Tezos',
                'General',
                'XTZ',
                '11.80',
                '4.61',
                '402.93',
                '845,98M',
                [360, 363, 366, 361, 366, 350, 370],
              ],
              [
                5,
                'Tezos',
                'General',
                'XTZ',
                '11.80',
                '4.61',
                '402.93',
                '845,98M',
                [360, 363, 366, 361, 366, 350, 370],
              ],
            ],
          })
          .forceRender()
      }, 5000)
    },
  },
}
