import Chartist from 'chartist'
import { Grid, h, createRef as gCreateRef } from 'gridjs'
import 'gridjs/dist/theme/mermaid.css'
// const dataURL = 'https://xtz.cbddev.com/kusd-price-output.php'

export default {
  data() {
    return {
      tokenArray: '',
      allTokenArray: '',
      rankingGrid: '',
      chartOpts: {
        height: '5.2rem',
        showPoint: true,
        fullWidth: true,
        chartPadding: { top: 0, right: 0, bottom: 0, left: 0 },
        axisX: { showGrid: false, showLabel: false, offset: 0 },
        axisY: { showGrid: false, showLabel: false, offset: 0 },
      },
    }
  },
  created() {
    this.fetchTokenRanking()
    console.log('created', this.tokenArray)
    this.fetchAllTokens()
    console.log('created', this.allTokenArray)
  },
  mounted() {
    console.log('mount', this.dataList)
    this.rankingGrid = new Grid({
      columns: [
        'No.',
        'Token',
        'Price',
        {
          name: '24hr',
          formatter: (cell) => {
            return h(
              'div',
              {
                style: {
                  color: cell > 0 ? '#27e4bb' : '#f3566b',
                },
              },
              `${cell} %`
            )
          },
        },
        {
          name: '7 day',
          formatter: (cell) => {
            return h(
              'b',
              {
                style: {
                  color: cell > 0 ? '#27e4bb' : '#f3566b',
                },
              },
              `${cell} %`
            )
          },
        },
        {
          name: 'Market Cap',
          formatter: (cell) => `$ ${cell}`,
        },
        'Liquidity',
        'Circulating Supply',
        {
          name: 'Last 7 Days',
          sort: false,
          formatter: (cell) => {
            const ref = gCreateRef()
            const chart = h('div', { ref })

            setTimeout(() => {
              ref.current &&
                new Chartist.Line(
                  ref.current,
                  {
                    series: [cell],
                  },
                  this.chartOpts
                )
            }, 0)

            return chart
          },
        },
      ],
      sort: true,
      fixedHeader: true,
      style: {
        table: {
          width: '100%',
          whiteSpace: 'nowrap',
          fontFamily: 'Cera Pro, sans-serif',
          fontWeight: '500',
          textTransform: 'capitalize',
          textAlign: 'center',
        },
        th: {
          background: '#2c7df7',
          color: '#fff',
          padding: '0.6rem 1.5rem',
          border: 'none',
        },
        td: {
          border: 'none',
          padding: '1rem 1.5rem',
          background: 'transparent',
        },
      },
      data: [
        [
          1,
          this.tokenArray.Token,
          this.tokenArray.Price,
          '10.20',
          '-11.80',
          '4.61',
          '$ 402.93',
          '845,98M',
          [360, 363, 366, 361, 366, 350, 370],
        ],
        [
          2,
          'Tezos',
          '$ 5.44',
          '10.20',
          '-11.80',
          '4.61',
          '$ 402.93',
          '845,98M',
          [360, 363, 366, 361, 366, 350, 370],
        ],
        [
          3,
          'Tezos',
          '$ 5.44',
          '10.20',
          '-11.80',
          '4.61',
          '$ 402.93',
          '845,98M',
          [360, 363, 366, 361, 366, 350, 370],
        ],
        [
          4,
          'Tezos',
          '$ 5.44',
          '10.20',
          '-11.80',
          '4.61',
          '$ 402.93',
          '845,98M',
          [360, 363, 366, 361, 366, 350, 370],
        ],
        [
          5,
          'Tezos',
          '$ 5.44',
          '10.20',
          '-11.80',
          '4.61',
          '$ 402.93',
          '845,98M',
          [360, 363, 366, 361, 366, 350, 370],
        ],
      ],
    }).render(document.querySelector('#rankingGrid'))
    this.updateRankings()
  },
  methods: {
    async fetchTokenRanking() {
      const tokenArray = await fetch(
        'https://xtz.cbddev.com/kusd-price-output.php',
        {
          method: 'GET',
          headers: {
            'Content-Type': 'application/json',
          },
        }
      ).then((res) => res.json())
      this.tokenArray = await tokenArray
      // console.log('First', this.tokenArray.Price)
      // console.log('second', tokenArray)
      // eslint-disable-next-line no-eval
      // this.tokenArray = eval(
      //   JSON.stringify(tokenArray).replace(/['"]+/g, ' ').replace(':', ':')
      // )
      // eslint-disable-next-line no-eval
      // this.tokenArray = eval(JSON.parse(this.tokenArray))
      // console.log('Stringify', this.tokenArray)
    },
    async fetchAllTokens() {
      const allTokenArray = await fetch(
        'https://xtz.cbddev.com/array-merge.php',
        {
          method: 'GET',
          headers: {
            'Content-Type': 'application/json',
          },
        }
      ).then((res) => res.json())
      this.allTokenArray = await allTokenArray
      console.log('All Tokens', this.allTokenArray[0].Price)
    },
    updateRankings() {
      setTimeout(() => {
        document.querySelector('#rankingGrid .gridjs').style.height =
          document.querySelector('#rankingGrid .gridjs').offsetHeight + 'px'
      }, 100)
      setTimeout(() => {
        this.rankingGrid
          .updateConfig({
            data: [
              [
                1,
                this.allTokenArray[0].Token,
                this.allTokenArray[0].Price,
                this.allTokenArray[0].TwentyFourhr,
                this.allTokenArray[0].SevenDays,
                this.allTokenArray[0].Market,
                this.allTokenArray[0].Liquidity,
                this.allTokenArray[0].Supply,
                this.allTokenArray[0].LastSeven,
                // [360, 363, 366, 61, 366, 150, 370],
              ],
              [
                2,
                this.allTokenArray[1].Token,
                this.allTokenArray[1].Price,
                this.allTokenArray[1].TwentyFourhr,
                this.allTokenArray[1].SevenDays,
                this.allTokenArray[1].Market,
                this.allTokenArray[1].Liquidity,
                this.allTokenArray[1].Supply,
                this.allTokenArray[1].LastSeven,
                // [360, 363, 366, 361, 366, 350, 370],
              ],
              [
                3,
                this.allTokenArray[2].Token,
                this.allTokenArray[2].Price,
                this.allTokenArray[2].TwentyFourhr,
                this.allTokenArray[2].SevenDays,
                this.allTokenArray[2].Market,
                this.allTokenArray[2].Liquidity,
                this.allTokenArray[2].Supply,
                this.allTokenArray[2].LastSeven,
                // [360, 363, 366, 361, 366, 350, 370],
              ],
              [
                4,
                this.allTokenArray[3].Token,
                this.allTokenArray[3].Price,
                this.allTokenArray[3].TwentyFourhr,
                this.allTokenArray[3].SevenDays,
                this.allTokenArray[3].Market,
                this.allTokenArray[3].Liquidity,
                this.allTokenArray[3].Supply,
                this.allTokenArray[3].LastSeven,
                // [360, 363, 366, 361, 366, 350, 370],
              ],
              [
                5,
                'Tezos',
                '$ 5.44',
                '10.20',
                '-11.80',
                '4.61',
                '$ 402.93',
                '845,98M',
                [360, 363, 366, 361, 366, 350, 370],
              ],
            ],
          })
          .forceRender()
      }, 2500)
    },
  },
}
